* {margin:0; padding:0; box-sizing: border-box;}
ul, li, ol {list-style: none;}

.slider3 {
  height:400px; 
  background-image: url("../public/img/slider3.jpg");
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
}

*{font-family:Arial, Helvetica, sans-serif}

/* Slider Carousel */

/* 카테고리 */
.section1 {background-color:none;}
.section1 .col-md-2 {margin:15px auto; display:flex; justify-content: center;}

/* 인기상품 */
.section2 {background-color:#F9F9F9;}
.section2 .imgbox {overflow: hidden;}
.section2 img {transition: all 0.5s;}
.section2 img:hover {transform:scale(1.1);}
.section2 .row {padding:10px 10px 40px 10px;}
.section2 .pop {background-color:#fff;}
.section2 span {display:block;}


.category{
  text-decoration: none; color:#000; text-align:center; background-color:none;
}
.category > p {margin: 10px 0 0 0px; font-size: 15px; font-weight:bold;}
.category:hover {color:black;}

.pop {background-color:#fff; margin: 10px 5px; padding:20px;}

/* Info */
.section3 {margin-top:40px;}
.section3 .pic {display:flex; justify-content: center; padding:20px 0 0 0px;}
.section3 .icon {display:flex; justify-content: center;}


/* trendingitems */
.section4 {background-color:#F9F9F9;}
.section4 .imgbox {overflow: hidden;}
.section4 img {transition: all 0.5s;}
.section4 img:hover {transform:scale(1.1);}
.section4 .tre {background-color:#fff;}
.section4 .row {padding:10px 10px 40px 10px;}
.section4 span {display:block;}

/* Banner1 */
.section5 {margin-top: 50px;}
.section5 .container {margin:0 auto;}
.section5 .imgbox {overflow: hidden;}
.section5 img {transition:all 0.5s;}
.section5 img:hover {transform:scale(1.05);}

/* newitems */
.section6 {background-color: #F9F9F9;}
.section6 .imgbox {overflow: hidden;}
.section6 img {transition: all 0.5s;}
.section6 img:hover {transform:scale(1.1);}
.section6 .newest {background-color: #fff;}
.section6 .row {padding:20px 20px 40px 20px;}
.section6 span {display:block;}

/* slider3 */
.slider3 {text-align: center;}
.slider3 {display:flex; justify-content: center;}
.slider3 .slidertext {width:500px; color:#fff;  padding-top: 150px;}

.section7 {width: 100%; height:auto; padding: 50px 0px 5px 0px; background-color: #eee;}
.section7 .copyright {border-top :1px solid #333;}
.section7 .col-md-3 .foot1 p { color: #333; line-height: 15px;}
.section7 .col-md-3 .foot1 p:hover {color: #84B53C; transition:all 0.3s}
.section7 .col-md-3 .foot2 p {color: #333; line-height: 15px;}
.section7 .col-md-3 .foot2 p:hover {color: #84B53C; transition:all 0.3s}
.section7 .col-md-3 .foot3 p {color: #333;}
.section7 .col-md-3 .foot3 .snsbox {margin: 20px; display:flex; justify-content: space-evenly;}

/* season items */
.section8 {background-color:#F9F9F9;}
.section8 .imgbox {overflow: hidden;}
.section8 img {transition: all 0.5s;}
.section8 img:hover {transform:scale(1.1);}
.section8 .sea {background-color:#fff;}
.section8 .row {padding:10px 10px 40px 10px;}
.section8 span {display:block;}


/* Subpage */

.subnav {width:100%; background:#eee;}
.subnav ul li:first-child:hover {cursor:pointer; color:#bfdf91; transition: all 0.3s;}
.subnav ul li:nth-child(3):hover {cursor:pointer; color:#bfdf91; transition: all 0.3s;}
.subnav ul li:nth-child(5):hover {cursor:pointer; color:#bfdf91; transition: all 0.3s;}
.subsection {width:100%; height:auto; background-color:#F9F9F9;}

.about_tb {width:100%; border-collapse:collapse; line-height:40px; border-top:2px solid #333; border-bottom:2px solid #333;}
.about_tb th,td {border-bottom:1px solid #ddd; padding:0px 20px; border-left:1px solid #ddd;}
.about_tb th:first-child, td:first-child {border-left:none;}

.about_tb2 {width:100%; border-collapse:collapse; line-height:40px; border-top:2px solid #333; border-bottom:2px solid #333; margin:30px 0px;}
.about_tb2 th,td {border-bottom:1px solid #ddd; padding:5px 10px; border-left:1px solid #ddd; font-size:18px;}
.about_tb2 th:first-child {border-left:none;}

/* Cart */

.cart_table {text-align:center;}
.cart_table thead {background-color: #eee; border-bottom:3px solid #bfdf91;}
.cart_table tbody tr td .text { margin-top: 50px;}
